<template>
  <div class="parallex">
    <div class="fixed-navbar fixed-top" :class="{'show': showFixedNavbar}" ref="fixedNavbar">
      <navbar/>
    </div>
    <section id="profile" ref="profile" style="background-color: rgba(0,0,0,.5);">
      <navbar :theme="'white'"/>
      <div class="profile pt-5">
        <div class="container">
          <div class="row">
            <div class="col-md-3 col-6 ml-auto mr-auto mb-5">
              <img class="profile-img" src="/static/images/profile.jpg"/>
            </div>
            <div class="col-md-9 mb-5">
              <div class="b-line">
                <div class="row">
                  <label class="col-3 col-form-label">Name</label>
                  <div class="col-9 pt-2">서강민</div>
                </div>
              </div>
              <div class="b-line">
                <div class="row">
                  <label class="col-3 col-form-label">Birth</label>
                  <div class="col-9 pt-2">1980.03.19</div>
                </div>
              </div>
              <div class="b-line">
                <div class="row">
                  <label class="col-3 col-form-label">Address</label>
                  <div class="col-9 pt-2">서울시 중구 장충동2가 186-1</div>
                </div>
              </div>
              <div class="b-line">
                <div class="row">
                  <label class="col-3 col-form-label">Email</label>
                  <div class="col-9 pt-2">rivermin.seo@gmail.com</div>
                </div>
              </div>
              <div class="b-line">
                <div class="row">
                  <label class="col-3 col-form-label">Social</label>
                  <div class="col-9">
                    <div class="row">
                      <div class="col-md-6">
                        <a href="#" class="btn btn-block text-left text-white">
                          <i class="mdi mdi-facebook"></i>
                        </a>
                      </div>
                      <div class="col-md-6">
                        <a href="#" class="btn btn-block text-left text-white">
                          <i class="mdi mdi-github"></i>
                        </a>
                      </div>
                    </div>                    
                  </div>
                </div>
              </div>              
            </div>
          </div>
        </div>
      </div>    
    </section>  
    <section class="bg-white mb-3 pt-5 pb-5" id="education">
      <div class="container">
        <div class="section-title">Education</div>
        <edu-timeline v-for="(item, index) of eduAndExp" :key="index" :payload="item"></edu-timeline>
      </div>
    </section>  
    <section class="bg-white mb-3 pt-5 pb-5" style="min-height: 2000px;" id="skills">
      <div class="container">
        <div class="section-title">Skills</div>
        <div class="row">
          <div class="col-md-4">
            <div>
              <strong>Language</strong>
              <star-score v-for="(item, index) of language" 
                :key="index" 
                :payload="item" 
                :color="'orange'"/>     
            </div>
          </div>
          <div class="col-md-4">
            <div>
              <strong>Framework</strong>
              <star-score v-for="(item, index) of frameWork" 
                :key="index" 
                :payload="item" 
                :color="'blue'"/>     
            </div>
          </div>
          <div class="col-md-4">
            <div>
              <strong>OS</strong>
              <star-score v-for="(item, index) of opSystem" 
                :key="index" 
                :payload="item" 
                :color="'blue'"/>     
            </div>
          </div>
        </div>
      </div>
    </section>
    <section class="footer pt-3 pb-3">
      <div class="container">
        <div class="text-center text-white">
          <strong>Created By Rivermin</strong>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Navbar from './ui/Navbar'
import EduTimeline from './ui/EduTimeline'
import StarScore from './ui/StarScore'
export default {
  name: 'HomePage',
  components:{
    Navbar,
    EduTimeline,
    StarScore
  },
  data () {
    return {
      showFixedNavbar: false,      
      headerHeight: 0,
      eduAndExp: [
        {
          date_range: '2005.02',
          desc: '광주 진흥 고등학교 졸업'          
        },
        {
          date_range: '2005.03 - 2012.02',
          desc: '광주 조선대학교 신소재 공학부 졸업'          
        },
        {
          date_range: '2012.03 - 2012.09',
          desc: '한빛 소프트 학원 수료 (자바 정부프레임워크)'          
        }
      ],
      language: [
        {
          label: 'Java',
          score: 4
        },
        {
          label: 'Javascript',
          score: 5
        },
        {
          label: 'Typescript',
          score: 2
        },
        {
          label: 'Nodejs',
          score: 5
        },
        {
          label: 'HTML&CSS',
          score: 5
        },
        {
          label: 'Swift',
          score: 3
        },
        {
          label: 'Kotlin',
          score: 4
        }
      ],
      frameWork: [
        {
          label: 'VueJS', score: 5
        },
        {
          label: 'Spring', score: 3
        },
        {
          label: 'NuxtJS', score: 4
        },
        {
          label: 'Android', score: 4
        },
        {
          label: 'IOS', score: 3
        }
      ],
      opSystem: [
        {
          label: 'Linux', score: 5
        },
        {
          label: 'Window', score: 3
        },
        {
          label: 'OSX', score: 3
        }        
      ]
    }
  },
  mounted () {
    this.headerHeight = this.$refs.profile.clientHeight        
    window.addEventListener('scroll', this.scrollListener)
  },
  methods: {
    scrollListener (e) {
      const scrollY = window.scrollY || document.documentElement.scrollTop
      this.showFixedNavbar = (scrollY > this.headerHeight)
      console.log(this.showFixedNavbar)
    }
  }
}
</script>

<style lang="scss" scoped>
.container{
  max-width: 960px;
}

.fixed-navbar{
  top: -200px;
  transition: top .3s;
  &.show{
    top: 0;
  }
}

.profile{
  color: white;
  .profile-img{
    width: 100%;
    border-radius: 50%;
  }
}

.b-line{
  border-bottom: 1px solid rgba(255, 255, 255, .5);
}

section{  
  .section-title{
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
  }
}

.parallex{  
  background-image: url('/static/images/bg-parallex-pc.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;  
}
.center-board{
  color: white;
  text-align: center;
  width: 100%;  
  .short-desc{
    display: flex;
    width: 200px;
    margin: 0 auto;
    .v-bar{
      display: flex;
      flex-grow: 1;
      align-items: center;
      div{
        width: 100%;
        height: 2px;
        background-color: white;
      }
    }
    .txt{
      display: flex;
      flex-grow: 2;
      justify-content: center;
    }
  }   
}
.page-container{  
  position: fixed;  
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  overflow-y: auto;
  .top-bar{
    border-bottom: 1px soild #ddd;
    background-color: white;  
    padding: .5rem 0;  
    opacity: 0;
  }
  .page-content{        
    background-color: #efefef;    
  }
}


@media screen and (max-width: 480px) {
  .parallex{
    background-image: url('/static/images/bg-parallex-mob.jpg');
  }
}

</style>



